import { Component, OnInit, Input, Injector } from '@angular/core';
import { Item } from 'src/app/services/item';
import { Observable } from 'rxjs';
import { environment } from "../../../environments/environment";
import { ItemVariation } from 'src/app/services/item-variation';
import { User } from '../../services/user';
import { Cart } from 'src/app/services/cart';
import { Router } from '@angular/router';
import { BasePage } from 'src/app/pages/base-page/base-page';


@Component({
    selector: 'app-shop-item',
    templateUrl: './shop-item.component.html',
    styleUrls: ['./shop-item.component.scss'],
})
export class ShopItemComponent extends BasePage {
    enableMenuSwipe(): boolean {
        throw new Error("Method not implemented.");
    }

    @Input() item: Item;
    @Input('customObservable') scrollObservable: Observable<any>;
    public selectedVariation: ItemVariation;

    constructor(private userService: User, private cartService: Cart, public router: Router, injector: Injector
    ) {
        super(injector);

    }

    ngOnInit() {
    }

    async onAddToCart() {

        try {



            if (!User.getCurrent()) {
                const user = await this.userService.loginAnonymously();
                window.dispatchEvent(new CustomEvent('user:login', { detail: user }));
            }

            const rawItem = Object.assign({}, this.item.toJSON());

            const allowed: any = ['objectId'];

            const filteredItem: any = Object.keys(rawItem)
                .filter(key => allowed.includes(key))
                .reduce((obj, key) => {
                    obj[key] = rawItem[key];
                    return obj;
                }, {});

            filteredItem.qty = 1;

            if (this.selectedVariation) {
                filteredItem.variation = this.selectedVariation.toJSON();
            }

            let cart = await this.cartService.getOne();
            cart = cart || new Cart;

            const existInCart = cart.items.find((item: any) => {

                if (item.variation) {
                    return item.objectId === filteredItem.objectId && item.variation.objectId === filteredItem.variation.objectId;
                }
                return item.objectId === filteredItem.objectId
            })

            if (existInCart) {

                return this.showToast("El producto ya se encuentra en tu carro")

            }

            cart.items.push(filteredItem);

            await cart.save();

            window.dispatchEvent(new CustomEvent('cart:updated', {
                detail: cart
            }));

            this.showToast("Producto agregar a tu carro de compras")

        } catch (err) {
        }
    }


    goTo(item) {
        this.router.navigate(['1/home/items/' + item.slug]);
        console.log("sad")
    }

}
